:root {
  --spinner-border-radius: 4px;
}
.spinner {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 4px;
  cursor: text;
  position: relative;
  height: var(--input-height, 36px);
  border-radius: var(--spinner-border-radius);
}
.spinner input {
  border: none!important;
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  line-height: 36px;
  font-size: var(--input-font-size);
  padding: 0 4px!important;
  text-align: center;
}
.spinner input:focus {
  box-shadow: none!important;
}
.spinner .button {
  height: calc(100% - 4px);
  aspect-ratio: 1;
  text-align: center;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  padding: 0;
  user-select: none;
}
.spinner .spinner-button-plus {
  order: 3;
}
.spinner .spinner-button-minus {
  order: 1;
}
.spinner input {
  order: 2;
}
.spinner.buttons-left .spinner-button-plus {
  order: 2;
  margin-left: 2px;
}
.spinner.buttons-left .spinner-button-minus {
  order: 1;
}
.spinner.buttons-left input {
  order: 3;
  text-align: right;
}
.spinner.buttons-right .spinner-button-plus {
  order: 3;
  margin-left: 2px;
}
.spinner.buttons-right .spinner-button-minus {
  order: 2;
}
.spinner.buttons-right input {
  order: 1;
  text-align: left;
}
.spinner.pill-input {
  --spinner-border-radius: calc(var(--input-height) * 0.44);
}
.spinner.pill-input .button {
  border-radius: 50%;
}
