:root {
  --player-background: #f2f3f5;
  --player-info-background: #f2f3f5;
  --player-info-color: #191919;
  --player-control-background-hover: #dadbdd;
  --player-constrol-color-hover: #191919;
}
.dark-side {
  --player-background: #191919;
  --player-info-background: #191919;
  --player-info-color: #f2f3f5;
  --player-control-background-hover: #4e5055;
  --player-constrol-color-hover: #f2f3f5;
}
.media-player {
  display: flex;
  position: relative;
  width: 100%;
  background: var(--player-background);
  height: auto;
  overflow: hidden;
  z-index: 1;
  padding: 4px 9px;
  border-radius: 20px;
}
.media-player video {
  width: 100%;
  height: 100%;
  z-index: 1;
}
.media-player .logo {
  display: block;
  position: absolute;
  top: 1.25rem;
  right: 1.25rem;
  z-index: 3;
  height: 32px;
}
.media-player .logo img {
  height: 32px;
}
.media-player .preloader {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.media-player .preloader.show {
  display: block;
}
.media-player .controls {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: auto;
  z-index: 2147483647;
  background-color: transparent;
  gap: 4px;
}
.media-player .controls.outside {
  position: relative;
}
.media-player .stream {
  display: flex;
  padding: 0;
  order: 5;
  width: 100%;
  min-width: 100px;
  position: relative;
  margin: 0;
}
.media-player .stream .slider {
  height: 0.875rem;
}
.media-player .load-audio {
  padding: 0;
  display: block;
  position: absolute;
  bottom: 4px;
  left: 0.625rem;
  width: calc(100% - 1.25rem);
}
.media-player button:focus {
  box-shadow: none!important;
}
.media-player .loop {
  order: 1;
}
.media-player .play {
  order: 2;
}
.media-player .stop {
  order: 3;
}
.media-player .info-box {
  position: relative;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  order: 4;
  background: var(--player-info-background);
  color: var(--player-info-color);
  padding: 0.625rem;
  font-size: 10px;
  flex-shrink: 0;
  white-space: nowrap;
  border: none;
}
.media-player .mute {
  order: 6;
}
.media-player .volume {
  order: 7;
  min-width: 50px;
  padding: 0;
  display: flex;
  gap: 4px;
}
.media-player .full {
  order: 8;
}
.media-player .loop,
.media-player .stop,
.media-player .play,
.media-player .mute {
  background: transparent;
}
@media (hover: hover) {
  .media-player .loop:hover,
  .media-player .stop:hover,
  .media-player .play:hover,
  .media-player .mute:hover {
    background: var(--player-control-background-hover);
    color: var(--player-constrol-color-hover);
  }
}
:-webkit-full-screen {
  width: 100%;
  height: 100%;
  z-index: 2147483647;
}
:-ms-fullscreen {
  width: 100%;
}
.media-player.full-screen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2147483647;
  max-height: 100%!important;
  height: 100%!important;
}
.audio-player {
  overflow: visible;
}
.audio-player .controls {
  position: relative;
}
